"use client"

import { useEffect, useRef } from "react"
import * as THREE from "three"
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'

export default function ThreeScene() {
  const mountRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (!mountRef.current) return

    // Scene setup
    const scene = new THREE.Scene()
    const camera = new THREE.PerspectiveCamera(75, 1, 0.1, 1000)
    const renderer = new THREE.WebGLRenderer({ alpha: true, antialias: true })
    
    const size = Math.min(500, window.innerWidth - 40)
    renderer.setSize(size, size)
    mountRef.current.appendChild(renderer.domElement)

    // Create Earth
    const earthGeometry = new THREE.SphereGeometry(1, 64, 64)
    const earthTexture = new THREE.TextureLoader().load('/assets/3d/texture_earth.jpg')
    const earthMaterial = new THREE.MeshPhongMaterial({
      map: earthTexture,
      bumpMap: earthTexture,
      bumpScale: 0.05,
    })
    const earth = new THREE.Mesh(earthGeometry, earthMaterial)
    scene.add(earth)

    // Add ambient light (dim)
    const ambientLight = new THREE.AmbientLight(0xffffff, 0.1)
    scene.add(ambientLight)

    // Add directional light (sun)
    const sunLight = new THREE.DirectionalLight(0xffffff, 1)
    sunLight.position.set(5, 0, 5)
    scene.add(sunLight)

    // Add a subtle blue atmosphere
    const atmosphereGeometry = new THREE.SphereGeometry(1.01, 64, 64)
    const atmosphereMaterial = new THREE.MeshPhongMaterial({
      color: 0x0077be,
      transparent: true,
      opacity: 0.1,
      side: THREE.BackSide
    })
    const atmosphere = new THREE.Mesh(atmosphereGeometry, atmosphereMaterial)
    scene.add(atmosphere)

    camera.position.z = 2.5

    // Add OrbitControls
    const controls = new OrbitControls(camera, renderer.domElement)
    controls.enableDamping = true
    controls.dampingFactor = 0.05
    controls.rotateSpeed = 0.5
    controls.minDistance = 1.5
    controls.maxDistance = 4

    // Initial rotation to show the sunlit side
    earth.rotation.y = Math.PI / 2

    // Animation
    const animate = () => {
      requestAnimationFrame(animate)
      earth.rotation.y += 0.001
      atmosphere.rotation.y += 0.001
      controls.update()
      renderer.render(scene, camera)
    }

    animate()

    // Handle resize
    const handleResize = () => {
      const size = Math.min(500, window.innerWidth - 40)
      camera.aspect = 1
      camera.updateProjectionMatrix()
      renderer.setSize(size, size)
    }

    window.addEventListener("resize", handleResize)

    return () => {
      mountRef.current?.removeChild(renderer.domElement)
      window.removeEventListener("resize", handleResize)
    }
  }, [])

  return <div ref={mountRef} className="h-full w-full" />
}

